import React, { useEffect } from 'react'

import { Link } from 'react-router-dom'

import { Button, Label, Logo } from 'components' // removed helmet
import styled from 'styled-components'

import useAffiliates from 'hooks/useAffiliates'

import { multichain } from 'services/multichain'

import { getAffiliates, setAffiliates } from 'helpers/storage'
import { media } from 'helpers/style'

import { IS_TESTNET } from 'settings/config'

import * as Styled from './Home.style'

const MainLogo = styled.div`
  margin-left: 0;
  ${media.sm`
    margin-top: 15px;
  `}
`

const Home = () => {
  const refs = useAffiliates()
  console.log(refs)
  useEffect(() => {
    if (refs && getAffiliates() === null) {
      const affiliateAddresses: string[] = refs
      const filteredAddresses = affiliateAddresses.filter((affiliateAddress) =>
        multichain.validateAddress({
          chain: 'THOR',
          address: affiliateAddress,
        }),
      )
      setAffiliates(filteredAddresses)
    }
  }, [refs])

  return (
    <Styled.HomeContainer>
      {/* <Helmet
          title="Crosschain.Quest"
          content="Find your way across blockchains without a central exchange."
        /> */}
      {IS_TESTNET ? (
        <Styled.NameWrapper>
          <Label color="normal">
            Crosschain.Quest has not yet launched but take a peak at the swap
            UI.
          </Label>
          <Styled.ActionContainer>
            <Link to="/swap/BTC.BTC_THOR.RUNE">
              <Button size="large" round fixedWidth={false}>
                Continue
              </Button>
            </Link>
          </Styled.ActionContainer>
        </Styled.NameWrapper>
      ) : (
        <Styled.NameWrapper>
          <Label color="normal">
            Find your way across blockchains without a centralized exchange.
          </Label>
          <Styled.ActionContainer>
            <Link to="/swap/BTC.BTC_THOR.RUNE">
              <Button size="large" round fixedWidth={false}>
                Continue
              </Button>
            </Link>
          </Styled.ActionContainer>
        </Styled.NameWrapper>
      )}
      <MainLogo>
        <Logo type="ccqswap" />
      </MainLogo>
    </Styled.HomeContainer>
  )
}

export default Home
